import React ,{Component} from 'react';
import {
    Form,
    Input,
    Button,
    Breadcrumb,
    Row,
    Modal ,
    message
} from 'antd';
import axios from 'axios'
function success() {
  Modal.error({
    title: '您已经输入100字',
    content: '您已经输入规定字数，请暂停输入',
  });
}
class AddPingForm extends Component{
      constructor(props){
        super(props)
      }
  componentDidMount(){
    var key=sessionStorage.getItem('key')
    console.log(key);
  }

  
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(key);
            if(loginid!='' && user!=""){
                console.log(key);
                axios.post('/index.php/zhi/ping',{
                  bid:key,
                  content:values.content,
                  name:user,
                  num:loginid
             }).then((res)=>{
                    if(res.data.code==200){
                     console.log('提交评论成功')
                      Modal.success({
                        title: '提交评论成功',
                        content: '您已经成功提交评论',
                      });
                   }else if(res.data.code==500){
                    Modal.error({
                      title: '提交评论失败',
                      content: res.data.message,
                    });
                   }else {
                    Modal.error({
                      title: '提交评论失败',
                      content: '提交评论失败，请重新提交',
                    });
                   }
             }).catch((error)=>{
                   console.log(error)
             })
            }
        }

    })
}


editping=()=>{
   this.props.history.push('/projectmanage/projectInformation/editping')
}
changebox=(e)=>{
     console.log(e.target.value.length);
     if(e.target.value.length==100){
      success();
     }
}


      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
          <div className="ping">
              <div className="bread">
                <Breadcrumb>
                    <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                    <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">添加评论 </Breadcrumb.Item>
                </Breadcrumb>
            </div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">评价内容：</p>
            <br/>
            <Form.Item >
            {getFieldDecorator('content', {
                })(
            <TextArea rows={10}   autosize={{ minRows: 10, maxRows: 50 }}  style={{width:1000}} placeholder="请输入文本，20-100个字数限制，禁止违禁语句(例如包含政治、宗教、犯罪、辱骂等),字" maxLength={100} allowClear onChange={this.changebox}/>,
                )}
            </Form.Item >
            <br/>
            <br/>
            <p><span style={{ fontSize:18 ,color:"#000",marginLeft:20}}>提示:&nbsp;&nbsp;</span>  房价&nbsp;&nbsp; &nbsp;&nbsp;地址&nbsp;&nbsp;&nbsp;&nbsp; 楼盘详情&nbsp;&nbsp; &nbsp;&nbsp;活动优惠 &nbsp;&nbsp;&nbsp;&nbsp;配套&nbsp;&nbsp;&nbsp;&nbsp; 限购&nbsp;&nbsp;&nbsp;&nbsp; 开盘时间 &nbsp;&nbsp;&nbsp;&nbsp;交通情况 &nbsp;&nbsp;&nbsp;&nbsp;装修情况&nbsp;&nbsp;&nbsp;&nbsp; 层高&nbsp;&nbsp;&nbsp;&nbsp; 产权年限&nbsp;&nbsp;&nbsp;&nbsp; 物业 </p>
        </Row>
        
      
        <br/>
        <Row>
            <Form.Item>
                <Button type="primary" style={{marginRight:800}}  onClick={this.editping}>
                 历史评论
                </Button>
                <Button type="primary" htmlType="submit" className="sub">
                提交
                </Button>
            </Form.Item>

        </Row>
      
          </Form>




          </div>
          )
      }
}
const AddPing=Form.create()(AddPingForm);
  export default AddPing